@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@200;300;400;600&family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  font-family: 'Assistant', sans-serif;
}
body{
  background: #eee;

}
.App {
  border: 2px solid #fff;
    padding: 35px 35px;
    text-align: center;
    width: 550px;
    margin: 135px auto;
    background: #fff;
    height: 550px;
    border-radius: 25px;
    background-size: cover;
    box-shadow: 3px 3px #ccc;

}
h1{
  font-family: 'Roboto Condensed', sans-serif;
}

.rain{
  background-image: url(data:image/jpeg;base64,);
}
.clouds{
  background-image:url(/static/media/clouds-img.15d8aff8.jpg);
}

.clear {  
  background-image:url(/static/media/clear-img.18350a89.jpg);
}

.haze{
  background-image:url(data:image/jpeg;base64,);
  
}
.smoke{
  background-image:url(/static/media/smoke-img.540c5135.jpg);

}

.hidden{
  display: none;
}


.place {
  text-transform: capitalize;
    margin-top: 22px;
    font-weight: 700;
    font-size: 1.3em;
}
.weather {
  font-size: 1em;
  margin-top: 12px;
  text-transform: capitalize;
}
.temp {
  font-size: 8em;
    font-weight: 100;
    color: white;
    text-shadow: 1px 1px grey;
}

.icon {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.image-icon{
  margin: 0 auto;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.first{
  width: 960px;
  margin: 0 auto;
}

input{
  width: 100%;
  height: 45px;
  border: 2px solid #3F51B5;
    padding: 10px;
  margin-top:20px;
  font-size: 20px;
}
.result {
  position: relative;
}

span.temp {
  font-size: 49px;
    position: absolute;
    top: 100%;
    left: 45%;
}

